// Order-btn-fixed
.order-btn-fixed
  position: fixed
  right: 5rem
  bottom: 5rem
  z-index: 500
  transform: translateX(0)
  transition: transform ease v.$transition-main
  &_hidden
    transform: translateX(100rem)
    // z-index: -1
    // visibility: hidden




/* Order modal */
.order-modal
  
  &__title
    text-align: center
    font-size: 24px
    p
      margin-bottom: 0.5em
  &__subtitle
    text-align: center
    font-size: 18px
    p
      margin-bottom: 1em
  .modal__content
    overflow-x: hidden
    overflow-y: auto
    &::-webkit-scrollbar
      width: 5px
      background: #eeeeee
      border-radius: 5px
    &::-webkit-scrollbar-thumb
      background: transparentize(v.$grey-primary, .5)
      border-radius: 5px

                // ::-webkit-scrollbar-button
                // ::-webkit-scrollbar
                // ::-webkit-scrollbar-button
                // ::-webkit-scrollbar-thumb
                // ::-webkit-scrollbar-track

/* Modal form */
.modal-form
  &-wrap 
    padding: 1px
  &__inner
    display: flex
    flex-direction: column
    flex-wrap: wrap
  .form-checkbox-wrap
    margin-bottom: 1em
    font-size: 14px
  .form-control,
  .form-control-message
    padding: 2rem
  .form-control
    height: auto
  .form-label
    top: 1.5em














